import React, { useEffect, useState } from 'react'
import styles from './index.module.scss'
import BackgroundTopImg from '../../assets/images/background_top.png'
import BackgroundBottomImg from '../../assets/images/background_bottom.png'
import BasemapImg from '../../assets/images/basemap.png'
import TopLogoImg from '../../assets/images/top_logo.png'
import TitleImg from '../../assets/images/title.png'
import BottomLogoImg from '../../assets/images/bottom_logo.png'
import { ReactComponent as SearchImg } from '../../assets/icons/search.svg'
import { RankModel } from '../../model/rankModel'
import { useAppDispatch, useAppSelector } from '../../app/hooks'
import { RootState } from '../../app/store'
import { getRanks } from '../../slices/meoSlice'

const Home = () => {
  const dispatch = useAppDispatch()
  const { ranks } = useAppSelector((state: RootState) => state.meo)
  const { result = [] } = ranks
  const [searchValue, setSearchValue] = useState('')

  useEffect(() => {
    dispatch(getRanks(searchValue))
  }, [searchValue])

  useEffect(() => {
  }, [result])

  return (
    <div className={styles.container}>
      <img className={styles.basemapImg} src={BasemapImg} alt='' />
      <img className={styles.topLogoImg} src={TopLogoImg} alt='' />
      <img className={styles.backgroundTop} src={BackgroundTopImg}></img>
      <img className={styles.backgroundBottom} src={BackgroundBottomImg}></img>
      <div className={styles.body}>
        <div className={styles.titleImg}>
          <img src={TitleImg} alt='' />
        </div>
        <div className={styles.buttons}>
          <button onClick={() => window.open('https://www.redbull.com/tw-zh/events/meo-tw')}><p>詳細賽事資訊</p></button>
          <button onClick={() => window.open('https://participate.redbull.com/zh/events/red-bull-meo-aov-taiwan-2023/2023')}><p>報名參賽</p></button>
        </div>
        <div className={styles.title}>百大排行榜</div>
        <div className={styles.subtitle}>
          {'每日中午'}
          <p>12:00</p>
          {'更新前一天的排行'}
        </div>
        <div className={styles.mobileSubtitle}>
          <div className={styles.col}>
            {'每日中午'}
            <p>12:00</p>
          </div>
          {'更新前一天的排行'}
        </div>
        <div className={styles.search}>
          <SearchImg />
          <input value={searchValue} onChange={(event) => setSearchValue(event.target.value)} />
        </div>
        <div className={styles.table}>
          <div className={styles.header}>
            <div className={styles.rank}>名次</div>
            <div className={styles.id}>遊戲ID</div>
            <div className={styles.score}>分數</div>
          </div>
          {result.length !== 0 && <div className={styles.content}>
            {result.map((data: RankModel, index: number) => (
              <div className={styles.cell} key={index}>
                <div className={`${styles.rank} ${data.special && styles.special}`}>{data.meoRank}</div>
                <div className={`${styles.id} ${data.special && styles.special}`}>{data.roleName}</div>
                <div className={`${styles.score} ${data.special && styles.special}`}>{data.masterScore}</div>
              </div>
            ))}
          </div>}
        </div>
        <img className={styles.bottomLogo} src={BottomLogoImg} alt='' />
      </div>
    </div>
  )
}

export default Home
